
html{
  font-size: 62.5%; 
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
}

/* layout */
.grid-container{
  display: grid;
  grid-template-areas: 
  'header'
  'main'
  'footer';
  ;
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%
}
header{
  grid-area: header;
  background-color: #203040;
}
main{
  grid-area: main;
  padding: 1rem;
}

footer{
  grid-area: footer;
  background-color: #203040;
  color: #ffffff;
}

/* common */

a {
  text-decoration: none;
}
h1{
  font-size: 1.8rem;
  padding: 1rem 0;
}
h2{
  font-size: 1.6rem;
  padding: 1rem 0;
}

a:hover {
  color: #ff8000
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

li{
  margin-top: 1rem;
}

input,
select,
textarea,
button {
  padding: 1rem;
  border-radius: 0.5rem;
  border:0.1rem #a4a4a4;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button {
  background-color: #f8f8f8;
  cursor: pointer;
}
input:hover,
select:hover,
textarea:hover,
button:hover {
  border: 0.1rem #404040 solid
}

button.primary{
  background-color: #ff8000;
}


button.block{
  width: 100%
}

button.small{
  font-size: 1.2rem;
}

/* header */
header a {
  color: #ffffff;
  padding: 1rem;
}
a.brand{
  color: #ffffff;
  font-size: 3rem;
  font-weight: bold;
}

.badge{
  background-color: #f02020;
  color:#ffffff;
  border-radius: 50%;
  padding: 0.2rem 0.7rem;
  font-size: 1.4rem;
  margin-left:0.2rem ;
}
.row{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.row.center {
  justify-content: center;
}
.row.top {
  align-items: flex-start;
}

.col-1{
  flex: 1 1 25rem;
  padding-left: 20px;
}
.col-2{
  flex: 2 1 50rem;
  
}
.min-30 {
  min-width: 30rem;
}


/* image */
img {
  border-radius: 0.5rem;
}

img.small{
  max-width: 5rem;
  width: 100%
}

img.medium {
  max-width: 29rem;
  width: 100%;
}

img.large {
  width: 100%;
  
}

/* card */
.card {
  border: 0.1rem #c0c0ca solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}

.card-body{
  padding: 1rem;
}

.card-body > * {
  margin-bottom: 0.5rem;
}

.price{
  font-size: 2rem;
}

/* rating */
.rating span{
  color: #f0c040;
  margin: 0.1rem;
}

.rating span:last-child{
  color: #203040;
}

/* Alert */
.loading{
  display: block !important;
}
.success {
  color: #20a020;
}
.danger {
  color: #a02020;
}
.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
}
.alert-info {
  color: #2020a0;
  background-color: #aeaed6;
}
.alert-danger {
  color: #a02020;
  background-color: #ffe0e0e0;
}
.alert-success {
  color: #20a020;
  background-color: #eeffe0;
}



/* form */

.form{
  max-width: 60rem;
  margin: 0 auto;
  border: 2px solid rgb(231, 172, 7)

}
input{
  border-radius: 0.5rem;
  border: 2px solid

}
.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}

.form label {
  margin: 1rem 0;
 
}

/* dropdown */
.dropdown{
  display: inline-block;
  position: relative;
}

.dropdown-content {
  position: absolute;
  display: none;
  right: 0;
  min-width: 12rem;
  padding: 1rem;
  z-index: 1;
  background-color: #203040;
  margin: 0;
  margin-top: 0.4rem;
  border-radius:0.5rem ;

}

.dropdown:hover .dropdown-content {
  display: block;
}

/* checkout steps */

.checkout-steps > div {
  border-top: 0.3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1;
  padding: 1rem;
  font-weight: bold;
}

.checkout-steps > div.active{
border-top-color: #ff8000;
color: #ff8000;
}

.table {
  width: 100%;
  border-collapse: collapse;

}

.table tbody tr:nth-of-type(odd){
  background-color: #fafafa;
}

.table td, .table th {
  text-align: left;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem
}

.table button {
  margin: 0 0.2rem
}